body {
  margin: 0;
  display: flex;
}

html {
  margin: 0;
  padding: 0;
}

header {
  background: #fff;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  display: flex;
  box-shadow: 2px 4px #0000000d;
}

.logo {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  padding: 50px 10px;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  display: flex;
}

.nav__item:first-child, .nav__item:last-child {
  margin-right: 0;
}

.link {
  color: #000;
  height: 15px;
  margin-right: 20px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
}

.link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  flex-shrink: 0;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: -20px;
  left: 0;
}

